<script>
export default {
  name: "Navbar",
  mounted() {
    window.onscroll = () => {
      if (document.body.scrollTop > 80 || document.documentElement.scrollTop > 80) {
        document.getElementById("mainNav").classList.add("scrolled")
      } else {
        document.getElementById("mainNav").classList.remove("scrolled");
      }
    }
  }
}
